<template>
  <v-card :class="{'overflow-hidden': isOverlayVisible}">
    <v-card-title class="pt-4 pb-0">
      <h4>{{ $t('form.login') }}</h4>
    </v-card-title>
    <v-card-text class="pa-4">
      <v-form v-model="formValid">
        <v-text-field
          v-model="form.email"
          :label="$t('form.email')"
          required
          :rules="[rules.required, rules.email]"
        />
        <v-text-field
          v-model="form.password"
          :label="$t('form.password')"
          type="password"
          required
          :rules="[rules.required]"
        />
      </v-form>

      <v-btn
        color="primary"
        variant="elevated"
        block
        :disabled="!formValid || loading"
        :loading="loading"
        @click="handleLogin"
      >{{ $t('form.login') }}</v-btn>

      <p class="auth-form__help">
        {{ $t('form.dontHaveAccount') }}
        <a href="#" @click.prevent="$emit('switchForm')">
          {{ $t('form.signUp') }}
        </a>
      </p>
    </v-card-text>

    <div class="form-overlay" v-if="isOverlayVisible">
      <div class="form-overlay__content">
        <template v-if="loading">
          <v-progress-circular indeterminate color="primary" />
        </template>
        <template v-else>
          <v-icon color="primary" size="x-large">
            mdi-check
          </v-icon>
          <p class="mb-4 mt-4">Login successful! Welcome back.</p>
          <a
            href="#"
            @click.prevent="$emit('authCompleted')"
          >
            {{ context === 'order' ? 'Back to checkout' : 'Close' }}
          </a>
        </template>
      </div>
    </div>
  </v-card>
</template>

<script>
import { ref, reactive, computed } from 'vue';
import { useUserStore } from "@/stores/userStore";

export default {
  props: {
    context: String,
  },
  setup() {
    const userStore = useUserStore();

    const form = reactive({
      email: '',
      password: '',
    });

    const formValid = ref(false);

    const showSuccessMessage = ref(false);

    const rules = {
      required: (v) => !!v || "This field is required",
      email: (v) => /.+@.+\..+/.test(v) || "Email must be valid",
    };

    const handleLogin = () => {
      if (!formValid.value) return;
      try {
        userStore.login(form);
        showSuccessMessage.value = true;
      } catch (error) {
        console.error(error);
      }
    };

    const loading = computed(() => userStore.isLoading);

    const isOverlayVisible = computed(() => showSuccessMessage.value || loading.value);

    return {
      loading,
      form,
      formValid,
      rules,
      isOverlayVisible,
      handleLogin,
    };
  },
};
</script>
